<template>
    <div class="container">
        <h3 class="mb-md">资产类别管理</h3>
        <el-form :model="ruleForm" ref="ruleForm" label-width="100px" class="demo-ruleForm">
            <el-row class="search-wrap mb-lg">
                <el-row>
                    <el-col :span="8">
                        <el-form-item label="关键字" prop="keywords">
                            <el-input v-model="ruleForm.keywords" placeholder="名称/库存编号/使用人"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="8">
                        <el-form-item label="申请时间" prop="applyDate">
                            <el-date-picker
                                    v-model="ruleForm.applyDate"
                                    type="daterange"
                                    placeholder="选择日期范围">
                            </el-date-picker>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="24" class="t-r">
                        <el-button @click="onSearch('ruleForm')" type="primary">确 定</el-button>
                        <el-button @click="isClear('ruleForm')">清除</el-button>
                    </el-col>
                </el-row>
            </el-row>
        </el-form>
        <el-row class="t-l">
            <el-col :span="6" style="width: 120px;margin:0 0 10px 0" >
                <el-button type="primary" @click="showPopup('add')" style="width:100%;padding: 5px">新增</el-button>
            </el-col>
        </el-row>
        <el-row >
            <el-table :data="tableData" @sort-change="onsorter" @filter-change="onfilter"
                      border style="width: 100%" @expand="tbclick"
                    >
              <!--  :row-key="getRowKeys "
                :expand-row-keys="expands"-->
                <el-table-column    type="expand"><!--:type="hasChild==1?expand:''"-->
                    <template  scope="props">
                        <el-form label-position="left" inline class="demo-table-expand">
                            <template>
                                <el-table border :data="tableChlid"
                                        style="width: 40%">
                                    <el-table-column
                                            prop="name"
                                            label="详情类别"
                                            width="200">
                                    </el-table-column>
                                    <el-table-column
                                            prop="update_time"
                                            label=""
                                            width="250">
                                    </el-table-column>
                                    <el-table-column prop="operation" label="" >
                                        <template scope="scope">
                                            <el-button  @click="showPopup('edit_child',scope.row)"
                                                        type="text" size="small" width="100">编辑
                                            </el-button>
                                        </template>
                                    </el-table-column>
                                </el-table>
                            </template>
                        </el-form>
                    </template>
                </el-table-column>
                <el-table-column type="index" prop="index" label="序号" width="200"></el-table-column>
                <el-table-column prop="name" label="类别" ></el-table-column>
                <el-table-column prop="update_time" label="更新时间" sortable="custom" width="250"></el-table-column>
                <el-table-column prop="operation" label="操作" >
                    <template scope="scope">
                        <el-button  @click="showPopup('edit',scope.row)" type="text" size="small">编辑</el-button>
                        <el-button  @click="showPopup('addDetails',scope.row)" type="text" size="small">新增详情类别</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </el-row>
        <el-row class="t-r mt-lg">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentpage"
                    :page-sizes="[10, 20, 30, 40]"
                    :page-size="pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total">
            </el-pagination>
        </el-row>

      <!--  新增弹窗-->
        <el-row >
            <transition name="fade">
                <el-col :span="24" class="Popup_background" v-show="Popup" transition="fade">
                    <div class="el-message-box">
                        <div class="el-message-box__header">
                            <div class="el-message-box__title" v-if="parameter=='add'">新增类别</div>
                            <div class="el-message-box__title" v-if="parameter=='edit'">编辑类别</div>
                            <div class="el-message-box__title" v-if="parameter=='addDetails'">新增详情类别</div>
                            <div class="el-message-box__title" v-if="parameter=='edit_child'">详情类别编辑</div>
                            <button type="button" aria-label="Close" class="el-message-box__headerbtn">
                                <i class="el-message-box__close el-icon-close" @click="hidePopup()"></i>
                            </button>
                        </div>
                        <div class="el-message-box__content">
                            <el-form :model="PopupForm" :rules="rules" ref="PopupForm"
                                     label-width="100px" class="demo-ruleForm">
                                <el-form-item label="类别-添加" prop="names" v-if="parameter=='add'">
                                    <el-input v-model="PopupForm.names"></el-input>
                                </el-form-item>
                                <el-form-item label="类别-编辑" prop="names" v-if="parameter=='edit'">
                                    <el-input v-model="PopupForm.names"></el-input>
                                </el-form-item>
                                <el-form-item label="详情_类别" prop="add_details" v-if="parameter=='addDetails'">
                                    <el-input v-model="PopupForm.add_details" type="textarea"
                                              :rows="4" placeholder="输入详情类别<每行一个详情类别>"></el-input>
                                    <p>输入详情类别(每行一个详情类别)</p>
                                </el-form-item>
                                <el-form-item label="详情_类别编辑" prop="edit_child" v-if="parameter=='edit_child'">
                                    <el-input v-model="PopupForm.edit_child"></el-input>
                                </el-form-item>
                            </el-form>
                        </div>
                        <div class="el-message-box__btns">
                            <button type="button" class="el-button el-button--default" @click="hidePopup()">
                                <span>取消</span>
                            </button>
                            <button type="button" class="el-button el-button--default el-button--primary "
                                    @click="submitForm('PopupForm')">
                               <span>确定</span>
                            </button>
                        </div>
                    </div>

                </el-col>
            </transition>
        </el-row>
    </div>
</template>

<script>
    export default {
        components: {
        },
        name:'AssetClass',
        data(){
            return {
                ruleForm:{
                    keywords:'',
                    applyDate:''
                },
                PopupForm:{
                    add_details:'',
                    names:'',
                    edit_child:""
                },
                currentpage:1,
                pageSize:10,
                total:0,
                sort:'',
                filter:[],//筛选
                keywords:'',
                start_time:'',
                end_time:'',
                type:1,
                Popup:false,
                parameter:'',
                id:0,
                item:'',

                rules: {
                    names: [
                        {required: true, message: '输入类别', trigger: 'blur'}
                    ],
                    add_details:[
                        {required: true, message: '输入详情列表，', trigger: 'blur'}
                    ]
                },

        /*        hasChild:[],*/
                expand:'',
                data_all: [],
                myArr:[],
                data_child:[]
            };
        },
        created(){
            this.$store.dispatch('Assetclass_table',{type:1,page_size:10}).then(response=>{
                let data = response.data.data;
                this.currentpage = data.page.currentPage;
                this.total = data.page.totalCount;

                /*this.hasChild.push(data.res.has_child) ;*/
            });
            Date.prototype.Format = function (fmt) { //author: meizz
                var o = {
                    "M+": this.getMonth() + 1, //月份
                    "d+": this.getDate(), //日
                    "h+": this.getHours(), //小时
                    "m+": this.getMinutes(), //分
                    "s+": this.getSeconds(), //秒
                    "q+": Math.floor((this.getMonth() + 3) / 3), //季度
                    "S": this.getMilliseconds() //毫秒
                };
                if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
                for (var k in o)
                    if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
                return fmt;
            }
        },
        mounted() {
            // 在这里你想初始化的时候展开哪一行都可以了
            /*this.expands.push(this.tableChlid.id);*/
        },
        computed:{
            tableData(){
                return this.$store.state.assetClass.assetclass_table;
            },
            tableChlid(){
                return this.$store.state.assetClass.assetChild_table;
            },
        },
        methods:{
            getRowKeys(row) {
                return row.id;
            },
            tbclick(row,expanded) {
                console.log(row);
                console.log(expanded);
                this.$store.dispatch('AssetChild_table',{pid:row.id,page_size:100}).then(response =>{
                    if (response.data.code == 200) {

                       /* this.data_all.push(response.data.data.res);
                       console.log(this.data_all)*/

                      /*  this.data_child = this.data_all[this.data_all。length-1]]
                        console.log(this.data_child)
*/

                    }
                }
                );


            },
            showPopup(parameter,obj) {
                this.Popup = true;
                this.parameter=parameter;
                if(parameter == 'edit'){
                    this.id = obj.id;
                    this.PopupForm.names = obj.name;
                } else if(parameter == 'edit_child'){
                    this.id = obj.id;
                    this.PopupForm.edit_child = obj.name;
                }
                else if (parameter == 'add'){
                    this.PopupForm.names='';
                }
                else if (parameter == 'addDetails'){
                    this.id = obj.id;
                }
                else {
                    return false;
                }
            },
            hidePopup() {
                this.Popup = false;
                if(this.parameter == 'edit'){
                    return false;
                }
                else if(this.parameter == 'add'){
                    this.PopupForm.names='';
                }else if(this.parameter == 'addDetails'){
                    this.PopupForm.add_details='';
                }
                else if(this.parameter == 'edit_child'){
                    this.PopupForm.edit_child='';
                }
                else {
                    return false;
                }
            },
            //弹窗
            submitForm(popupForm) {
                let para = this.parameter;
                //添加类型
                if( para=='add'){
                    console.log("type_name:"+this.PopupForm.names);
                    this.$refs[popupForm].validate((valid) => {
                        if (valid) {
                            let type_name=this.PopupForm.names;
                            this.$store.dispatch('AssetclassAdd_table',{type_name:type_name}).then(response =>{
                                    if (response.data.code == 200) {
                                        this.ondispatch();
                                    }
                                }
                            );
                            this.Popup = false;
                        } else {
                            console.log('error submit!!');
                            return false;
                        }
                    });
                }
                //编辑类型
                else if (para=='edit')
                {
                    this.$refs[popupForm].validate((valid) => {
                        if (valid) {
                            let type_names=this.PopupForm.names;
                            let type_id = this.id;
                            this.$store.dispatch('AssetclassUpdate_table',{type_name:type_names,type_id:type_id}).then(response =>{
                                    if (response.data.code == 200) {
                                        this.ondispatch();
                                    }
                                }
                            );
                            this.Popup = false;
                        } else {
                            console.log('error submit!!');
                            return false;
                        }
                    });
                }
                else if (para=='edit_child')
                {
                    this.$refs[popupForm].validate((valid) => {
                        if (valid) {
                            let type_names=this.PopupForm.edit_child;
                            let type_id = this.id;
                            this.$store.dispatch('AssetclassUpdate_table',{type_name:type_names,type_id:type_id}).then(response =>{
                                    if (response.data.code == 200) {
                                       /* this.ondispatch();*/
                                    }
                                }
                            );
                            this.Popup = false;
                        } else {
                            console.log('error submit!!');
                            return false;
                        }
                    });
                }
                //添加资产子类别
                else if(para='addDetails'){
                    this.$refs[popupForm].validate((valid) => {
                        if (valid) {
                            let detail=this.PopupForm.add_details;
                            let type_id = this.id;
                            this.$store.dispatch('AssetAddChild_table',{child:detail,type_id:type_id}).then(response =>{
                                    if (response.data.code == 200) {
                                        this.ondispatch();
                                    }
                                }
                            );
                            this.Popup = false;
                        } else {
                            console.log('error submit!!');
                            return false;
                        }
                    });
                }
                else
                {
                    return false;
                }
            },
            searchData(value){
                if(value != null){
                    this.currentpage = value;
                }
                return {
                    type:this.type,
                    page:this.currentpage,
                    page_size:this.pageSize,
                    sort:this.sort,
                    keywords:this.keywords,
                    start_time:this.start_time,
                    end_time:this.end_time,
                    pid:this.pid
                };
            },
            ondispatch(){
                this.$store.dispatch('Assetclass_table',this.searchData()).then(response =>{
                        let data = response.data.data;
                        this.currentpage = data.page.currentPage;
                        this.total = data.page.totalCount;
                        this.pageSize = data.page.perPage;
                    }
                );
            },
            handleSizeChange(val){
                this.pageSize = val;
                this.ondispatch();
            },
            handleCurrentChange(val){
                this.currentpage = val;
                this.ondispatch();
            },
            isClear(formName){
                this.$refs[formName].resetFields();
                this.keywords ='';
                this.start_time='';
                this.end_time='';
                this.ondispatch();
            },
            onSearch(formName){
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.keywords = this.ruleForm.keywords;
                        if(this.ruleForm.applyDate[0] =='' || this.ruleForm.applyDate[0] == null || this.ruleForm.applyDate[1] =='' || this.ruleForm.applyDate[1] ==null){

                        }else{
                            this.start_time = this.ruleForm.applyDate[0].Format("yyyy-MM-dd");
                            this.end_time = this.ruleForm.applyDate[1].Format("yyyy-MM-dd");
                        }
                        this.ondispatch();
                    } else {
                        return false;
                    }
                });
            },
            onsorter({column,prop,order}){
                if( order == 'ascending' ) {
                    this.sort = 'asc';
                }else if(order == 'descending'){
                    this.sort = 'desc';
                }else{
                    this.sort = ''
                }
                this.ondispatch();
            },
            onfilter(column){
                this.filter = Object.values(column)[0];
                this.ondispatch();
            }
        }
    }
</script>
<style rel="stylesheet/less" lang="less" scoped>
    body {
        font-family: Helvetica, sans-serif;
      .Popup_background{
          position: fixed;
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
          padding-top: 16%;
          z-index: 10000;
          width:100%;
          height: 100%;
          text-align: center;
          overflow: auto;
          background: rgba(7,17,27,0.8);
      }
    }
</style>






