<template>
    <el-menu class="header" mode="horizontal" v-if="isnav == 1">
        <Hamburger class="hamburger-container" :toggleClick="toggleSideBar" :isActive="sidebar.opened"></Hamburger>
        <el-dropdown class="avatar-container" trigger="click">
            <div class="avatar-wrapper">
                <img class="user-avatar" src="../../assets/status_avatar.png" />{{name}}
                <i class="el-icon-caret-bottom"/>
            </div>
            <el-dropdown-menu class="user-dropdown" slot="dropdown">
                <el-dropdown-item><span @click="logout" style="display:block;">退出登录</span></el-dropdown-item>
            </el-dropdown-menu>
        </el-dropdown>
    </el-menu>
    <header v-else>
        <a herf="javascript:;" @click="checkurl"><span></span></a>
        <h1 class="title">{{ title }}</h1>
    </header>
</template>
<script>
    import { mapGetters } from 'vuex';
    import Hamburger from '../../components/Hamburger/index.vue';

    export default {
        name: 'Headbar',
        components: {
            Hamburger
        },
        data() {
            return {
                bodywidth:'',
                title:'',
                isClick:false,
                isChildClick:false,
            }
        },
        computed: {
            ...mapGetters([
                'sidebar',
                'name',
                'isnav'
            ]),

        },
        methods: {
            toggleSideBar() {
                this.$store.dispatch('ToggleSideBar',document.body.clientWidth);
            },
            logout() {
                this.$store.dispatch('LoginOut').then(response => {
                    location.href = response.data.data.login_url;
                });
            },
            checkurl(){
                if(this.$route.name == '首页'){
                    this.homelink();
                }else{
                    this.childlink();
                }
            },
            homelink(){
                //window.JavaJs.back();
            },
            childlink(){
                this.$router.push({path:'/adminhome',query:{isnav:0,pagenation:1}});
            }
        },
        created () {
            window.onresize = ()=>{
                this.toggleSideBar();
            }
            this.title = this.$route.name == '首页' ? '车城OA系统':this.$route.name;
        },
        watch: {
            '$route' (to, from) {
                this.title = to.name == '首页' ? '车城OA系统':to.name;
            }
        }
    }

</script>
<style rel="stylesheet/less" lang="less" scoped>
    @media screen and (max-width: 640px) {
        /*app头*/
        header {
            position: relative;
            padding: 20px 0 0;
            background-color: #FDC235;
            a {
                position: absolute;
                left: 0;
                top: 20px;
                padding: 13px 16px;
                span {
                    display: block;
                    width: 16px;
                    height: 16px;
                    background: url('../../assets/back.png') 0 0 no-repeat;
                    background-size: 100% 100%;
                }
                &:active {
                    -webkit-tap-highlight-color: transparent;
                }
            }
            .title {
                margin: 0 44px;
                line-height: 44px;
                text-align: center;
                font-size: 18px;

            }
        }
    }
    .header {
        height: 50px;
        line-height: 50px;
        border-radius: 0px !important;
        background-color: #32aafa;
        .hamburger-container {
            line-height: 58px;
            height: 50px;
            float: left;
            padding: 0 10px;
        }
        .avatar-container {
            height: 50px;
            display: inline-block;
            position: absolute;
            right: 0;
            .avatar-wrapper {
                cursor: pointer;
                position: relative;
                height: 50px;
                line-height: 50px;
                padding: 0 10px;
                color: #fff;
                .user-avatar {
                    float: left;
                    width: 30px;
                    height: 30px;
                    margin-right: 7px;
                    margin-top:10px;
                    border-radius: 10px;
                }
                .el-icon-caret-bottom {
                    margin-left: 5px;
                }
            }
        }
    }
</style>



