<template>
    <div class="container">
        <ConstModal
                :isconstshow="isconstshow"
                :constdata="constdata"
                :cpperson="cpperson"
                @Onconstsubmit="Onconstsubmit"
                @ToggleConstDialog="ToggleConstDialog"
                @DeleteConstPerson="DeleteConstPerson"
        />
        <CopyModal
                :iscopyshow="iscopyshow"
                :copydata="copydata"
                :cpperson="cpperson"
                @Oncopysubmit="Oncopysubmit"
                @ToggleCopyDialog="ToggleCopyDialog"
                @DeleteCopyPerson="DeleteCopyPerson"
        />
        <SubmitModal
                :issubmitshow="issubmitshow"
                :loading="loading"
                :leave_time="leave_time"
                :stock_status="stock_status"
                :finance_status="finance_status"
                :account_status="account_status"
                :work_status="work_status"
                :des="des"
                @Onsubmit="Onsubmit"
                @TogglesubmitDialog="TogglesubmitDialog"
        />
        <el-form :model="ruleForm" ref="ruleForm" label-width="150px" class="demo-ruleForm">
            <h3 class="mb-md">离职申请表</h3>
            <el-form-item label="离职日期：" prop="leave_time" :rules="[{ required: true, message: '请选择离职职日期'}]">
                <el-col :span="11">
                    <el-date-picker v-model="ruleForm.leave_time" type="date" placeholder="选择日期"></el-date-picker>
                </el-col>
            </el-form-item>
            <el-form-item label="离职原因：" prop="des" :rules="[{ required: true, message: '请输入离职原因'}]">
                <el-col :span="11">
                    <el-input type="textarea" v-model="ruleForm.des" placeholder="请输入离职原因"></el-input>
                </el-col>
            </el-form-item>
            <h3 class="mb-md">离职交接</h3>
            <el-form-item label="物资是否交还：" prop="stock_status" :rules="[{ required: true, message: '请确认物资是否交还'}]">
                <el-col :span="11">
                    <el-select v-model="ruleForm.stock_status" placeholder="请选择">
                        <el-option key="0" label="是" value="1" ></el-option>
                        <el-option key="1" label="否" value="0"></el-option>
                    </el-select>
                </el-col>
            </el-form-item>
            <el-form-item label="财务是否结算：" prop="finance_status" :rules="[{ required: true, message: '请确认财务是否结算'}]">
                <el-col :span="11">
                    <el-select v-model="ruleForm.finance_status" placeholder="请选择">
                        <el-option key="0" label="是" value="1" ></el-option>
                        <el-option key="1" label="否" value="0"></el-option>
                    </el-select>
                </el-col>
            </el-form-item>
            <el-form-item label="账号密码是否交接：" prop="account_status" :rules="[{ required: true, message: '请确认账号密码是否交接'}]">
                <el-col :span="11">
                    <el-select v-model="ruleForm.account_status" placeholder="请选择">
                        <el-option key="0" label="是" value="1" ></el-option>
                        <el-option key="1" label="否" value="0"></el-option>
                    </el-select>
                </el-col>
            </el-form-item>
            <el-form-item label="部门工作是否交接：" prop="work_status" :rules="[{ required: true, message: '请确认部门工作是否交接'}]">
                <el-col :span="11">
                    <el-select v-model="ruleForm.work_status" placeholder="请选择">
                        <el-option key="0" label="是" value="1" ></el-option>
                        <el-option key="1" label="否" value="0"></el-option>
                    </el-select>
                </el-col>
            </el-form-item>
            <el-form-item label="上传附件">
                <el-upload class="upload-demo" multiple name="files" :action="leave_time" :on-success="handlefileChange"  :on-remove="handlefileRemove">
                    <el-button size="small"><i class="el-icon-upload2"></i>  点击上传</el-button>
                </el-upload>
            </el-form-item>
            <h3>审批人和抄送人</h3>
            <el-form-item label="审批人">
                <const-copy-person :isapplyright="true" :choicetype="1" :data="constdata" @ToggleConstDialog="ToggleConstDialog"/>
            </el-form-item>
            <el-form-item label="抄送人">
                <const-copy-person :isapplyright="false" :choicetype="2" :data="copydata" @ToggleCopyDialog="ToggleCopyDialog"/>
            </el-form-item>
            <el-form-item label="">
                <el-button @click="onsubmitCheck('ruleForm')" type="primary">确 定</el-button>
                <el-button @click="isVisible('ruleForm')">取 消</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    import ConstCopyPerson from '../../components/ConstCopyPerson.vue';
    import ConstModal from '../../components/ConstModal/ConstModal.vue';
    import CopyModal from '../../components/CopyModal/CopyModal.vue';
    import SubmitModal from '../../components/applyleave/submitModal.vue';
    export default {
        name:'applyleave',
        components:{
            ConstCopyPerson,
            ConstModal,
            CopyModal,
            SubmitModal
        },
        data() {
            return {
                ruleForm: {
                    leave_time: '',
                    des: '',
                    stock_status: '',
                    finance_status: '',
                    account_status: '',
                    work_status: '',
                },
                leave_time:'',
                des: '',
                stock_status: '',
                finance_status: '',
                account_status: '',
                work_status: '',
                fileList: [],
                loading:false,
                isconstshow:false,
                iscopyshow:false,
                issubmitshow:false
            }
        },
        computed:{
            ApplyID(){
                return this.$store.state.applyleave.applyleave_addApplyID;
            },
            cpperson(){
                return this.$store.state.global.cpPerson;
            },
            constdata(){
                return this.$store.state.applyleave.applyleave_constdata;
            },
            copydata(){
                return this.$store.state.applyleave.applyleave_copydata;
            },
            options(){
                return this.$store.state.global.citys_data;
            }
        },
        created(){

        },
        methods: {
            ToggleTableDialog(data){//明细添加弹窗
                this.istableshow = data.istableshow;
            },
            deleteRow(index, rows) {
                rows.splice(index,1);
                this.$store.dispatch('Deleterows',rows);
            },
            handlefileChange(response, file, fileList){
                if (fileList.length > 7) {
                    message.error('上传文件数量已达上限!');
                } else{
                    this.fileList = fileList;
                }
                console.log(this.fileList);
            },
            handlefileRemove(file, fileList) {//图片移除
                this.fileList = fileList;
            },
            //提交选中审批人
            Onconstsubmit(data){
                this.$store.dispatch('Add_applyleave_constsubmit',data);
            },
            DeleteConstPerson(){
                this.$store.dispatch('Delete_applyleave_ConstPerson',data);
            },
            //添加审批人弹窗状态修改
            ToggleConstDialog(data){
                this.isconstshow = data.isconstshow;
            },
            //提交选中抄送人
            Oncopysubmit(data){
                this.$store.dispatch('Add_applyleave_copysubmit',data);
            },
            DeleteCopyPerson(){
                this.$store.dispatch('Delete_applyleave_CopyPerson',data);
            },
            //添加抄送人弹窗状态修改
            ToggleCopyDialog(data){
                this.iscopyshow = data.iscopyshow;
            },

            //核对表单弹窗状态修改
            TogglesubmitDialog(data){
                this.issubmitshow = data.issubmitshow;
            },


            Onsubmit(){
                this.loading=true;
                let constdata = this.$store.state.applyleave.applyleave_constdata;
                let resultconstdata = [],resulcopydata = [];
                if(constdata.length > 0){
                    for(let item in constdata){
                        resultconstdata.push(parseInt(constdata[item].split("/")[0]));
                    }
                }

                let copydata = this.$store.state.applyleave.applyleave_copydata;
                if(copydata.length > 0){
                    for(let item in copydata){
                        resulcopydata.push(parseInt(copydata[item].split("/")[0]));
                    }
                }

                let attachments = (this.fileList).map(data => data.response.data[0]);

                let data = {
                    apply_id:this.ApplyID,
                    leave_time:this.leave_time,
                    stock_status:this.stock_status,
                    finance_status:this.finance_status,
                    account_status:this.account_status,
                    work_status:this.work_status,
                    files:attachments,
                    approval_persons:resultconstdata,
                    copy_person:resulcopydata,
                };

                this.$store.dispatch('applyleave_Add',data).then( response => {
                    this.loading = false;
                    this.$router.push({ path: '/success' ,query:{apply_id:response.data.data,type:11}});
                }).catch(err => {
                    this.loading = false;
                });
            },
            onsubmitCheck(formName){
                let constdata = this.$store.state.applyleave.applyleave_constdata;
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        if (constdata.length > 0) {
                            this.issubmitshow = true;
                        } else {
                            this.$message.error('请填写完整后提交!');
                        }
                    } else {
                        return false;
                    }
                });
            }
        }
    };
</script>

<style rel="stylesheet/less" lang="less" scoped>
    .el-upload--picture-card{
        width: 80px !important;
        height:80px !important;
    }
    a{
        text-decoration: none;
        color:#108ee9;
    }
    a:hover{
        text-decoration: underline;
    }
</style>


