<template>
    <div class="container">
        <el-form :model="ruleForm" ref="ruleForm" label-width="150px" class="demo-ruleForm">
            <h3 class="mb-md">转正申请表</h3>
            <el-form-item label="入职日期：" prop="entry_time" label-width="150px" :rules="[{ required: true, message: '请选择入职日期'}]">
                <el-col :span="11">
                    <el-date-picker v-model="ruleForm.entry_time" type="date" placeholder="选择日期"></el-date-picker>
                </el-col>
            </el-form-item>
            <el-form-item label="试用期职位：" prop="job" label-width="150px" :rules="[{ required: true, message: '请选择试用期职位'}]">
                <el-col :span="11">
                    <el-select v-model="ruleForm.job" placeholder="请选择">
                        <el-option :key="key" :label="item.label" :value="item.value+'/'+item.label" v-for="(item,key) in positions"></el-option>
                        <!--<el-option key="0" label="产品经理" value="[1,'产品经理']" ></el-option>
                        <el-option key="1" label="php程序开发" value="[2,'php程序开发']"></el-option>-->
                    </el-select>
                </el-col>
            </el-form-item>
            <el-form-item label="试用期业绩自述：" prop="prosecution" label-width="150px" :rules="[{ required: true, message: '请输入试用期业绩自述'}]">
                <el-col :span="11">
                    <el-input type="textarea" v-model="ruleForm.prosecution" :rows="3" placeholder="请输入" style="width: 100%;"></el-input>
                </el-col>
            </el-form-item>
            <el-form-item label="试用期工作总结：" prop="summary" label-width="150px" :rules="[{ required: true, message: '请输入试用期工作总结'}]">
                <el-col :span="11">
                    <el-input type="textarea" v-model="ruleForm.summary" :rows="3" placeholder="请输入" style="width: 100%;"></el-input>
                </el-col>
            </el-form-item>
            <el-form-item label="对公司意见和建议：" prop="suggest" label-width="150px" :rules="[{ required: true, message: '请输入对公司意见和建议'}]">
                <el-col :span="11">
                    <el-input type="textarea" v-model="ruleForm.suggest" :rows="3" placeholder="请输入" style="width: 100%;"></el-input>
                </el-col>
            </el-form-item>
            <el-form-item label="上传附件：" label-width="150px">
                <el-upload class="upload-demo" multiple name="files" :action="BaseUrl" :on-success="handlefileChange">
                    <el-button size="small"><i class="el-icon-upload2"></i>  点击上传</el-button>
                </el-upload>
            </el-form-item>
            <h3 class="mt-md mb-md">审批人和抄送人</h3>
            <el-form-item label="审批人：" label-width="150px">
                <const-copy-person :isapplyright="true" :choicetype="1" :data="constdata" @ToggleConstDialog="ToggleConstDialog"/>
            </el-form-item>
            <el-form-item label="抄送人：" label-width="150px">
                <const-copy-person :isapplyright="false" :choicetype="2" :data="copydata" @ToggleCopyDialog="ToggleCopyDialog"/>
            </el-form-item>
            <el-form-item label="" label-width="150px">
                <el-button @click="onsubmitPositive('ruleForm')" type="primary">确 定</el-button>
                <el-button @click="isVisible('ruleForm')">取 消</el-button>
            </el-form-item>
        </el-form>
        <ConstModal
                :isconstshow="isconstshow"
                :constdata="constdata"
                :cpperson="cpperson"
                @Onconstsubmit="Onconstsubmit"
                @ToggleConstDialog="ToggleConstDialog"
                @DeleteConstPerson="DeleteConstPerson"
        />
        <CopyModal
                :iscopyshow="iscopyshow"
                :copydata="copydata"
                :cpperson="cpperson"
                @Oncopysubmit="Oncopysubmit"
                @ToggleCopyDialog="ToggleCopyDialog"
                @DeleteCopyPerson="DeleteCopyPerson"
        />

        <SubmitModal
                :issubmitshow="issubmitshow"
                :loading="loading"
                :job="job"
                :entry_time="entry_time"
                :prosecution="prosecution"
                :summary="summary"
                :suggest="suggest"
                @Onsubmit="Onsubmit"
                @TogglesubmitDialog="TogglesubmitDialog"
        />
    </div>
</template>

<script>
    import { mapGetters } from 'vuex'
    import ConstCopyPerson from '../../components/ConstCopyPerson.vue';
    import ConstModal from '../../components/ConstModal/ConstModal.vue';
    import CopyModal from '../../components/CopyModal/CopyModal.vue';
    import SubmitModal from '../../components/positive/SubmitModal.vue';
    export default {
        name:'prositive',
        components:{
            ConstCopyPerson,
            ConstModal,
            CopyModal,
            SubmitModal
        },
        data() {
            return {
                ruleForm: {
                    entry_time:'',
                    job:'',
                    prosecution:'',
                    summary:'',
                    suggest:'',
                },
                entry_time:'',
                job:'',
                jobid:'',
                prosecution:'',
                summary:'',
                suggest:'',
                //positions:[],
                isconstshow:false,
                iscopyshow:false,
                issubmitshow:false,
                loading:false,
            }
        },
        computed:{
            ...mapGetters([
                'BaseUrl'
            ]),
            cpperson(){
                return this.$store.state.global.cpPerson;
            },
            constdata(){
                return this.$store.state.positive.positive_constdata;
            },
            copydata(){
                return this.$store.state.positive.positive_copydata;
            },
            positions(){
                return this.$store.state.global.position_data;//职位选择初始化数据
            }
        },
        created(){
            this.$store.dispatch('Position_List');//职位数据初始化
        },
        methods: {
            isVisible(formName){
                this.issubmitshow = false;
                if(typeof(formName)!='function'){
                    this.$refs[formName].resetFields();
                }
            },
            handlefileChange(response, file, fileList){
                if (fileList.length > 7) {
                    message.error('上传文件数量已达上限!');
                } else{
                    this.fileList = fileList;
                }
            },
            handlefileRemove(file, fileList) {//图片移除
                this.fileList = fileList;
            },
            //提交选中审批人
            Onconstsubmit(data){
                this.$store.dispatch('Add_positive_constsubmit',data);
            },
            DeleteConstPerson(){
                this.$store.dispatch('Delete_positive_ConstPerson',data);
            },
            //添加审批人弹窗状态修改
            ToggleConstDialog(data){
                this.isconstshow = data.isconstshow;
            },

            //提交选中抄送人
            Oncopysubmit(data){
                this.$store.dispatch('Add_positive_copysubmit',data);
            },
            DeleteCopyPerson(){
                this.$store.dispatch('Delete_positive_CopyPerson',data);
            },
            //添加抄送人弹窗状态修改
            ToggleCopyDialog(data){
                this.iscopyshow = data.iscopyshow;
            },

            //核对表单弹窗状态修改
            TogglesubmitDialog(data){
                this.issubmitshow = data.issubmitshow;
            },
            Onsubmit(){
                this.loading=true;
                let constdata = this.$store.state.positive.positive_constdata;
                let resultconstdata = [],resulcopydata = [];
                    if(constdata.length > 0){
                        for(let item in constdata){
                            resultconstdata.push(parseInt(constdata[item].split("/")[0]));
                        }
                    }

                let copydata = this.$store.state.positive.positive_copydata;
                    if(copydata.length > 0){
                        for(let item in copydata){
                            resulcopydata.push(parseInt(copydata[item].split("/")[0]));
                        }
                    }
                let apply_id = this.$store.state.positive.positive_addApplyID;
                let fujian=[];
                    if(this.fileList != null){
                        fujian = (this.fileList).map(data => data.response.data);
                    }
                let data = {apply_id:apply_id,approval_persons:resultconstdata,copy_person:resulcopydata,fujian:fujian,job:this.jobid,prosecution:this.prosecution,summary:this.summary,suggest:this.suggest,entry_time:this.entry_time};
                this.$store.dispatch('Positive_Add',data).then( response => {
                    this.loading = false;
                    this.$router.push({ path: '/success' ,query:{apply_id:response.data.data,type:10}});
                }).catch(err => {
                    this.loading = false;
                });
            },
            onsubmitPositive(formName){
                let constdata = this.$store.state.positive.positive_constdata;
                let copydata = this.$store.state.positive.positive_copydata;

                if( constdata.length > 0){
                    this.$refs[formName].validate((valid) => {
                        if (valid) {
                            this.entry_time = this.ruleForm.entry_time.Format("yyyy-MM-dd");
                            this.job = this.ruleForm.job.split("/")[1];
                            this.jobid = parseInt(this.ruleForm.job.split("/")[0]);
                            console.log(this.ruleForm.prosecution);
                            this.prosecution = this.ruleForm.prosecution;
                            this.summary = this.ruleForm.summary;
                            this.suggest = this.ruleForm.suggest;
                            this.issubmitshow = true;
                        } else {
                            return false;
                        }
                    });
                }else{
                    this.$message.error('请填写完整后提交!');
                }

            }
        }
    };
</script>

<style rel="stylesheet/less" lang="less" scoped>
    .el-upload--picture-card{
        width: 80px !important;
        height:80px !important;
    }
    a{
      text-decoration: none;
      color:#108ee9;
    }
    a:hover{
        text-decoration: underline;
    }
</style>


