import Cookies from 'js-cookie';

const app = {
    state: {
        sidebar: {
            opened: !+Cookies.get('sidebarStatus')
        },
        isnav: localStorage.getItem('isnav') || '1',
        theme: 'default',
        livenewsChannels: Cookies.get('livenewsChannels') || '[]',
        pagenation:localStorage.getItem('pagenation') || '1',
    },
    mutations: {
        TOGGLE_SIDEBAR:(state,data) => {
            //console.log(state.isnav);
            if(state.isnav == 0){
                /*if (data <= 768) {
                    Cookies.set('sidebarStatus', 0);
                    state.sidebar.opened = !1;
                } else {
                    if(!state.sidebar.opened){
                        Cookies.set('sidebarStatus', 1);
                        state.sidebar.opened = !0;
                    }else{
                        Cookies.set('sidebarStatus', 0);
                        state.sidebar.opened = !1;
                    }
                }*/
            }else{
                if (state.sidebar.opened) {
                    Cookies.set('sidebarStatus', 1);
                } else {
                    Cookies.set('sidebarStatus', 0);
                }
                state.sidebar.opened = !state.sidebar.opened;
                console.log(state.sidebar.opened);
            }
        },
        SET_Isnav:(state,data) => {
            state.isnav = data;
        },
        SET_pagenation:(state,data) => {
            state.pagenation = data;
        }
    },
    actions: {
        ToggleSideBar: ({ commit },data) => {
            commit('TOGGLE_SIDEBAR',data)
        },
        SetIsnav: ({ commit },data) => {
            commit('SET_Isnav',data)
        },
        Setpagenation: ({ commit },data) => {
            commit('SET_pagenation',data)
        }
    }
};

export default app;


