<template>
    <div class="app-wrapper" :class="{'hideSidebar':!sidebar.opened}">
        <div class="sidebar-wrapper" v-if="isnav==1">
            <Sidebar class="sidebar-container"/>
        </div>
        <div class="main-container">
            <Headbar/>
            <app-main/>
        </div>
    </div>
</template>

<script>
    import { mapGetters } from 'vuex';
    import Headbar from './Headbar.vue';
    import Sidebar from './Sidebar.vue';
    import AppMain from './AppMain.vue';
    export default {
        name: 'layout',
        components: {
            Headbar,
            AppMain,
            Sidebar,
        },
        computed: {
            ...mapGetters([
                'isnav'
            ]),
            sidebar() {
                return this.$store.state.app.sidebar;
            }
        }
    }
</script>

<style rel="stylesheet/less" lang="less" scoped>

    .app-wrapper {
        position: relative;
        height: 100%;
        width: 100%;
        &.hideSidebar {
            .sidebar-wrapper {
                transform: translate(-180px, 0);
                .sidebar-container {
                    transform: translate(132px, 0);
                }
                &:hover {
                    transform: translate(0, 0);
                    .sidebar-container {
                        transform: translate(0, 0);
                    }
                }
            }
            .main-container{
                margin-left: 0px;
            }
        }
        .sidebar-wrapper {
            width: 180px;
            position: fixed;
            top: 0;
            bottom: 0;
            left: 0;
            z-index: 1001;
            overflow-x: hidden;
            transition: all .28s ease-out;
            @include scrollBar;
            .el-submenu__title{
                color: #bfcbd9;
            }
        }
        .sidebar-container {
            transition: all .28s ease-out;
        }
        .main-container {
            min-height: 100%;
            transition: all .28s ease-out;
            margin-left: 180px;
        }
    }
</style>


